.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.moviestyle {
  width: 250px;
  max-height: 500px;
  background-color: antiquewhite;
  border: 3px solid greenyellow;
  border-radius: 5px;
  margin: 10px 20px;
  padding: 30px;
}

.moviestyle_img {
  position: relative;
  top: -20px;
  width: 100%;
  box-shadow: 10px 10px 10px #888888;
}

.moviestyle_img:hover {
  transform: translateY(-10px);
  transition: transform 0.5s;
}
